@import url('https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700');
@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:400,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400');

body {
  font-family: "Open Sans", serif;
}

.wrapper {
  background: #fff;
  /* background: linear-gradient(to right, #3e4350, #f16051); */
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  
  border-radius:90px 30px 90px 30px;
  height: 80vh;
  background: #fff;
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  width: 80%;
margin: 0 auto;
  
}


.title-container {
  border: solid 2px #181818;
  border-radius: 90px 0px 0px 30px;
  height: 80vh;
  background: url("img/bg.jpg") center center no-repeat;
  background-size: cover;
  display: flex;
  border-right: groove 5px #e94d3b;
  /* align-items: center;
  justify-content: center; */
  text-align: center;
  color: #000;
}
.title-container-mobile {
  border: solid 2px #181818;
  border-radius: 90px 79px 0px 1px;
  height: 80vh;
  background: url("img/bg.jpg") center center no-repeat;
  background-size: cover;
  display: flex;
  /* border-right: groove 5px #e94d3b; */
  /* align-items: center;
  justify-content: center; */
  text-align: center;
  color: #000;
}

.title-container__title {
  font-size: 50px;
  color: #050505;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.title-container__subtitle {
  font-style: italic;
  font-weight: 100;
  color:  #141313;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: "Merriweather", serif;
}

.form-container {
  overflow:auto; 
  /* align-items: center;
  justify-content: center; */
  /* background-color: #2f3945; */
  background-color: #fff;
  border: solid 2px #181818;
  border-left-color: transparent;
  border-radius: 0px 30px 90px 0px;
  height: 80vh;
  padding-top: 100px !important;
  padding-left: 50px !important;
}
.form-container-mobile {
  overflow:auto; 
  /* align-items: center;
  justify-content: center; */
  /* background-color: #2f3945; */
  background-color: #fff;
  border: solid 2px #181818;
  /* border-left-color: transparent; */
  border-radius: 0px 0px 90px 90px;
  height: 80vh;
  padding-top: 30px !important;
  padding-left: 50px !important;
}


input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f16051;
  width: 30%;
  padding-bottom: 4px;
  color: rgb(51, 51, 51) !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] { 
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
    -webkit-text-fill-color: #fff !important;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #e94d3b;
  color: #fff !important;
  font-weight: 100;
}

button:active {
  outline: none;
}

.weather__info {
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}

.weather__key {
  color: #e94d3b;
  border-bottom: solid 1px #e97c70;
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: rgb(48, 47, 47);
  font-weight: 200;
}

.weather__error {
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}